export const Technical = (props) => {
  const { skill, expertise } = props;

  return (
    <div>
      {
        skill && (
          <span>
            { skill }
          </span>
        )
      }

      {
        expertise && (
          <span className="text-muted">
            &nbsp;({ expertise })
          </span>
        )
      }
    </div>
  )
}