export const Education = (props) => {
  const { school, location, course, start, end, score } = props;

  return (
    <div className="mb-5">
			<div className="d-flex gap-0-2">
				{
					start && end && (
						<div>
							{start}&nbsp;-&nbsp;{end}
						</div>
					)
				}

				<div style={{width: "350px"}}>

					{
						course && (
							<div className="course fw-bold">
								{course}
							</div>
						)
					}

					<div className="d-flex justify-content-between small school">
						{
							school && (
								<span className="name fw-bold">
									{school}

									{
										location && (
											<span className="location fw-500 text-muted">
												, {location}
											</span>
										)
									}
								</span>
							)
						}

						{
							score && (
								<span>
									{score}
								</span>
							)
						}
					</div>
				</div>
			</div>
    </div>
  )
}