import { nanoid } from "nanoid";

const AddSkills = (props) => {
  const { skills } = props;
  return skills.map((skill) => {
    return (
      <Technical
				key={nanoid()}
        skill={ skill }
      />
    );
  });
}

const AddWork = (props) => {
  const { work } = props;

  return work.map((data) => {
    return (
      <Work
				key={nanoid()}
        title={data.title}
        link={data.link}
        description={data.description}
				outcome={data.outcome}
      />
    );
  });
}

