import HTMLReactParser from "html-react-parser"; 

export const Work = (props) => {
  const { title, link, description, outcome } = props;

  return (
    <div>
      {
        title && (
          <h4>
						{HTMLReactParser(title)}
          </h4>
        )
      }

			{
				link && (
					<a className="text-muted link fw-600" href={link} target="_blank" rel="noopener noreferrer">
						{HTMLReactParser(link)}
					</a>
				)
			}

      {
        description && (
          <ul className="text-muted">
						{
							description.map((item) => <li>{HTMLReactParser(item)}</li>)
						}
          </ul>
        )
      }

			{
				outcome && (
					<p className="text-muted">
						{HTMLReactParser(outcome)}
					</p>
				)
			}
    </div>
  )
}